import { NextResponse } from 'next/server'
import { getBlockchainCounts } from '@/app/lib/clickhouse'

export async function GET() {
  try {
    const data = await getBlockchainCounts()

    // Transform data into a more usable format
    const metrics = {
      bitcoin_blocks: 0,
      bitcoin_transactions: 0,
      solana_blocks: 0,
      solana_transactions: 0,
      total_records: 0,
    }

    data.forEach((row) => {
      const count = parseInt(row.count, 10)
      const key = `${row.source.toLowerCase()}_${row.record_type.toLowerCase()}` as keyof typeof metrics

      if (key in metrics) {
        metrics[key] = count
        metrics.total_records += count
      }
    })

    return NextResponse.json(metrics)
  } catch (error) {
    const errorMessage = error instanceof Error ? error.message : 'Unknown error'
    console.error('API /data error:', errorMessage)

    return NextResponse.json(
      { error: `Failed to fetch blockchain data: ${errorMessage}` },
      { status: 500 }
    )
  }
}
