import { createClient } from '@clickhouse/client'

// Create ClickHouse client for querying data
// This runs server-side only and keeps credentials secure
export function getClickHouseClient() {
  return createClient({
    host: process.env.CLICKHOUSE_HOST || 'http://clickhouse:8123',
    username: process.env.CLICKHOUSE_USER || 'default',
    password: process.env.CLICKHOUSE_PASSWORD || 'clickhouse_password',
    database: process.env.CLICKHOUSE_DB || 'blockchain_data',
  })
}

// Query to get record counts by source
export async function getBlockchainCounts() {
  const client = getClickHouseClient()

  const query = `
    SELECT
      source,
      record_type,
      COUNT(*) as count
    FROM
      blockchain_raw_data
    GROUP BY
      source, record_type
    ORDER BY
      source, record_type
  `

  try {
    const result = await client.query({
      query,
      format: 'JSONEachRow',
    })

    const data = await result.json()
    return data as Array<{ source: string; record_type: string; count: string }>
  } catch (error) {
    console.error('ClickHouse query error:', error)
    throw error
  } finally {
    await client.close()
  }
}

// Query to get total data size
export async function getDataSize() {
  const client = getClickHouseClient()

  const query = `
    SELECT
      SUM(bytes) / (1024 * 1024 * 1024) as size_gb
    FROM
      system.tables
    WHERE
      database = '${process.env.CLICKHOUSE_DB || 'blockchain_data'}'
  `

  try {
    const result = await client.query({
      query,
      format: 'JSONEachRow',
    })

    const data = await result.json() as Array<{ size_gb: number }>
    return data[0]?.size_gb || 0
  } catch (error) {
    console.error('ClickHouse query error:', error)
    throw error
  } finally {
    await client.close()
  }
}
